import React, { Component } from "react";

export default class prac extends Component {
  state = {
    month: 0,
    startTime: Date.now(),
    currentTime: Date.now()
  };
  onChange = e => {
    this.setState({
      month: parseInt(e.target.value, 10)
    });
  };
  componentDidMount() {
    setInterval(() => {
      this.setState({
        currentTime: Date.now()
      });
    }, 100);
  }
  render() {
    const { month, startTime, currentTime } = this.state;
    const time = (currentTime - startTime) / 1000;
    return (
      <div>
        月薪:
        <input type="number" onChange={this.onChange} value={month} />
        <br /> 時薪:
        <label>{(month / 240).toFixed(1)}</label>
        <br />
        分薪:
        <label>{(month / 240 / 60).toFixed(1)}</label>
        <br />
        秒薪:
        <label>{(month / 240 / 60 / 60).toFixed(2)}</label>
        <br />
        偷取薪水:
        <label>{(time * (month / 240 / 60 / 60)).toFixed(2)}</label>
      </div>
    );
  }
}
